export class State {
  public id?: number;
  public name?: string;
  public abbreviation?: string;

  /**
   *  Construtor genrico
   *  TypeScript no permite mltiplos construtores
   *  Passa os argumentos em objeto
   *  Inicializa as variveis locais com o  objeto, mas apenas os existentes, os outros ficam default
   *  Serve como clone tambm
   **/
  constructor(o: State = {} as State) {
    let { id = undefined, name = undefined, abbreviation = undefined } = o;

    this.id = id;
    this.name = name;
    this.abbreviation = abbreviation;
  }
}
